import React, { useState, useCallback } from "react";
import Box from "../../components/Box";
import DropZone from "../../components/DropZone";
import { createObject } from "../../library/parse";
import NumberTextInput from "../CRUDForm/NumberTextInput";

const Component = ({ storeId, singleProductMode }) => {
  const [price, setPrice] = useState(5);
  const [messages, setMessages] = useState([]);
  const handleUpload = useCallback(
    async (files) => {
      const name = files[0]._metadata.name.replace(/\.[^/.]+$/, "");
      const result = singleProductMode
        ? [
            {
              name,
              startingPrice: price,
              closePrice: price,
              photos: files,
              quantity: 1,
            },
          ]
        : files.map((file) => ({
            name,
            startingPrice: price,
            closePrice: price,
            photos: [file],
            quantity: 1,
          }));
      const objects = await Promise.all(
        result.map(async (obj, i) => {
          const objectCreated = await createObject("Product", obj, {
            storeId,
          });
          return objectCreated;
        })
      );
    },
    [messages]
  );

  return (
    <Box>
      <NumberTextInput value={price} onChange={(v) => setPrice(v)} />

      <DropZone onComplete={handleUpload} />

      {messages}
    </Box>
  );
};

export default Component;
